.home-container {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-top-right {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
}

.home-logo {
  display: block;
  margin: 40px auto 20px auto;
  max-width: 300px;
  width: 100%;
}